<template>
  <div class="taskrun-statistical-count">
<!--    <div class="tool-bar">-->
      <!--      <el-link icon="el-icon-document-add" @click="onAdd">新增</el-link>-->
      <!--      <div class="split-line"></div>-->
      <!--      <el-link icon="el-icon-circle-check" @click="toggleEnable(1)">启用</el-link>-->
      <!--      <div class="split-line"></div>-->
      <!--      <el-link icon="el-icon-remove-outline" @click="toggleEnable(0)">停用</el-link>-->
      <!--      <div class="split-line"></div>-->
      <!--      <el-link icon="el-icon-refresh" @click="featchData()">刷新</el-link>-->
<!--    </div>-->
    <div class="grid-container">
      <el-row>
        <el-col :span="4">
          <el-tree
            :props="props"
            :load="loadNode"
            node-key="id"
            :default-expanded-keys="['root']"
            lazy
            @node-click="handleNodeClick"
          >
          </el-tree>
        </el-col>
        <el-col :span="20">
          <el-table
            border
            stripe
            ref="table"
            :data="grid.rows"
            :height="grid.height"
            highlight-current-row
            v-loading="grid.loading"
            @selection-change="val => grid.selectedRow =val"
          >
<!--            <el-table-column type="selection" align="center" width="50"></el-table-column>-->
            <el-table-column type="index" width="50" align="center" label="序号"></el-table-column>
            <el-table-column prop="jobName" label="作业处理器" align="left"  show-overflow-tooltip></el-table-column>
            <el-table-column prop="describe" label="描述" align="left"  show-overflow-tooltip></el-table-column>
            <el-table-column prop="executeFrequency" label="执行频率" align="left" width="200" show-overflow-tooltip>
              <template slot-scope="scope">{{scope.row.executeFrequency | cron}}</template>
            </el-table-column>
            <el-table-column prop="statisticalCount" label="统计范围" align="left" width="200" show-overflow-tooltip :formatter="formatRule"></el-table-column>
            <el-table-column align="center" width="100" label="操作">
              <template slot-scope="scope">
                <el-link
                  style="font-size: 20px;margin: 0 5px 0 0;"
                  icon="el-icon-edit-outline"
                  @click="onUpdate(scope.row)"
                ></el-link>
                <el-link
                  style="font-size: 20px;margin: 0 5px 0 0;"
                  icon="el-icon-view"
                  @click="onView(scope.row)"
                ></el-link>
                <!--              <el-link-->
                <!--                style="font-size: 20px;margin: 0 5px 0 0;"-->
                <!--                icon="el-icon-delete"-->
                <!--                @click="onDelete(scope.row)"-->
                <!--              ></el-link>-->
              </template>
            </el-table-column>
          </el-table>
        </el-col>
      </el-row>
    </div>
    <el-dialog
      v-dialog
      width="700px"
      v-if="dialog.show"
      :visible.sync="dialog.show"
      :modal-append-to-body="false"
      :close-on-click-modal="false"
    >
      <div slot="title" class="title">
        <i :class="dialogMode.icon"></i>
        <span>监控调度维护 —— {{dialogMode.title}}</span>
      </div>
      <el-form ref="form" :rules="dialog.rules" :model="dialog.data" label-width="110px">
        <el-row>
          <el-col :span="12">
            <el-form-item label="所属执行器" prop="executorName">
              <el-input disabled v-model="global.executorName"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="执行器地址" prop="address">
              <el-input disabled v-model="global.address"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="作业处理器" prop="jobName">
              <el-input disabled v-model="dialog.data.jobName"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="描述" prop="describe">
              <el-input disabled v-model="dialog.data.describe"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="执行频率" prop="executeFrequency">
              <el-input disabled :value="dialog.data.executeFrequency | cron"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
          <el-form-item label="统计范围" prop="statisticalCount">
            <el-input :disabled="dialog.mode===3" v-model="dialog.data.statisticalCount"></el-input>
          </el-form-item>
        </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="编制人" prop="userName">
              <el-input disabled v-model="dialog.data.userName"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="编制时间" prop="createTime">
              <el-date-picker
                disabled
                v-model="dialog.data.createTime"
                type="date"
                placeholder="选择日期"
                style="width: 100%;"
              ></el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer">
        <el-button type="primary" @click="onSave" icon="fa fa-save" v-if="dialog.mode !== 3" :disabled="dialog.loading">&nbsp;保存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import moment from 'moment'
export default {
  name: 'taskrun-statistical-count',
  data () {
    const validateThreshold = (rule, value, next) => {
      if (!value && value !== 0) {
        next('请输入阈值！')
      } else if (!/^(-?\d+)$/.test(value)) {
        next('请输入整数！')
      } else if (value <= 0) {
        next('请输入大于0的整数！')
      } else {
        next()
      }
    }
    return {
      props: {
        label: 'name',
        children: 'zones',
        isLeaf: 'leaf'
      },
      global: {
        groupId: null,
        executorName: null,
        address: null
      },
      grid: {
        rows: [],
        height: null,
        loading: false,
        selectedRow: []
      },
      dialog: {
        mode: null,
        show: false,
        loading: false,
        data: {
          fid: null,
          groupId: null,
          jobName: null,
          describe: null,
          executeFrequency: null,
          statisticalCount: null,
          createBy: null,
          userName: null,
          createTime: null
        },
        rules: {
          statisticalCount: [
            {
              required: true,
              validator: validateThreshold,
              trigger: 'blur'
            }
          ]
        }
      },
      jobExecutorSettins: [{
        jobName: 'xxxxxJob',
        describe: '011',
        executeFrequency: '1'
      }, {
        jobName: 'x1Job',
        describe: '011',
        executeFrequency: '1'
      }, {
        jobName: 'x2Job',
        describe: '011',
        executeFrequency: '1'
      }, {
        jobName: 'x3Job',
        describe: '011',
        executeFrequency: '1'
      }]
    }
  },
  computed: {
    dialogMode () {
      switch (this.dialog.mode) {
        case 1:
          return { title: '新增', icon: 'el-icon-document-add', message: '添加成功！' }
        case 2:
          return { title: '编辑', icon: 'el-icon-edit-outline', message: '修改成功！' }
        case 3:
          return { title: '查看', icon: 'el-icon-document' }
        default:
          return {}
      }
    }
  },
  created () {
    // this.featchData()
  },
  methods: {
    /**
     格式化 表格数据
     */
    formatRule: function (row, column) {
      if (!row.statisticalCount && row.statisticalCount !== 0) {
        return '未设置'
      }
      return '最近 ' + row.statisticalCount + ' 次'
    },
    /**
     获取 主页面表格数据
     */
    async featchData (groupId) {
      this.grid.loading = true
      let { data } = await this.$api.queryData({
        params: {
          mapperId: 'com.bosssoft.monitor.dao.JobExecutorThresholdMapper.statisticCountThresholds',
          groupId: groupId
        }
      })
      this.grid.loading = false
      this.grid.rows = data.data
    },
    /**
     获取 作业处理器下拉框数据
     */
    async featchjobExecutorsData () {
      let { data } = await this.$api.queryData({
        params: {
          mapperId: 'com.bosssoft.monitor.dao.JobExecutorThresholdMapper.jobExecutors'
        }
      })
      this.jobExecutorSettins = data.data
    },
    /**
     输入数字合法性 校验
     */
    async validateNumeric () {
      let regPos = /^(-?\d+)(\.\d+)?$/ // 浮点数
      if ((this.dialog.data.statisticalCount && !regPos.test(this.dialog.data.statisticalCount))) {
        this.$message.info('指标阈值必须为数字！')
        return Promise.reject(new Error())
      }
      if (!this.dialog.data.statisticalCount || this.dialog.data.statisticalCount <= 0) {
        this.$message.info('指标阈值必须＞0！')
        return Promise.reject(new Error())
      }
      return Promise.resolve()
    },
    /**
     新增对话框 初始化
     */
    async onAdd () {
      try {
        // await this.validateBeforeAdd()
        let model = {
          createBy: this.$store.state.user.userInfo.userId,
          userName: this.$store.state.user.userInfo.userName,
          createTime: moment().format('YYYY-MM-DD HH:mm:ss')
        }
        this.dialog.mode = 1
        this.dialog.show = true
        this.dialog.data = model
      } catch (error) {
        error.message && this.$message.error(error.message)
      }
    },
    /**
     查看对话框 初始化
     */
    onView (row) {
      this.dialog.mode = 3
      this.dialog.show = true
      this.dialog.data = Object.assign({}, row)
    },
    /**
     删除前 校验
     */
    async validateBeforeDelete (row) {
      if (row.status === 1) {
        this.$message.info('当前指标已启用，不允许删除！')
        return Promise.reject(new Error())
      }
      return Promise.resolve()
    },
    /**
     删除 操作
     */
    async onDelete (row) {
      try {
        await this.validateBeforeDelete(row)
        this.$confirm('您确定要删除当前选中的记录吗?', '确认删除', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async () => {
          await this.$api.remove({
            params: {
              mapperId: 'com.bosssoft.monitor.dao.JobExecutorThresholdMapper.deleteByPrimaryKey',
              fid: row.fid
            }
          })
          this.featchData()
          this.$message.success('删除成功！')
        }).catch(() => { })
      } catch (error) {
        error.message && this.$message.error(error.message)
      }
    },
    /**
     更新对话框弹出前 校验
     */
    // async validateBeforeUpdate (row) {
    //   if (row.status === 1) {
    //     this.$message.info('当前指标已启用，不允许编辑！')
    //     return Promise.reject(new Error())
    //   }
    //   return Promise.resolve()
    // },
    /**
     更新对话框 初始化
     */
    async onUpdate (row) {
      try {
        this.dialog.loading = false
        // await this.validateBeforeUpdate(row)
        this.dialog.mode = 2
        this.dialog.show = true
        this.dialog.data = Object.assign({}, row)
        this.dialog.data.groupId = this.global.groupId
        if (!this.dialog.data.userName) {
          this.dialog.data.createBy = this.$store.state.user.userInfo.userId
          this.dialog.data.userName = this.$store.state.user.userInfo.userName
          this.dialog.data.createTime = moment().format('YYYY-MM-DD HH:mm:ss')
        }
      } catch (error) {
        error.message && this.$message.error(error.message)
      }
    },
    /**
     保存前 校验
     */
    async validateBeforeSave () {
      await this.$refs['form'].validate()
      await this.validateNumeric()
      if (!this.dialog.data.jobName) {
        this.$message.info(`请选择作业处理器！`)
        return Promise.reject(new Error())
      }
      if (this.dialog.mode === 1) {
        // 新增保存前校验
        let { data } = await this.$api.queryData({
          params: {
            mapperId: 'com.bosssoft.monitor.dao.JobExecutorThresholdMapper.isExists',
            isList: false,
            jobName: this.dialog.data.jobName
          }
        })
        if (data.data.count > 0) {
          this.$message.info(`当前已存在启用的{${this.dialog.data.jobName}}指标，不允许新增！`)
          return Promise.reject(new Error())
        }
      }
      if (this.dialog.mode === 2) {
        // 编辑保存前校验
        let { data } = await this.$api.queryData({
          params: {
            mapperId: 'com.bosssoft.monitor.dao.JobExecutorThresholdMapper.isExists',
            isList: false,
            jobName: this.dialog.data.jobName
          }
        })
        if (data.data.count === 0) {
          this.dialog.mode = 1
        }
      }
      return Promise.resolve()
    },
    /**
     （新增 mode=1、更新 mode=2）保存 操作
     */
    async onSave () {
      try {
        await this.validateBeforeSave()
        this.dialog.loading = true
        if (this.dialog.mode === 1) {
          await this.$api.add({
            data: {
              mapperId: 'com.bosssoft.monitor.dao.JobExecutorThresholdMapper.insertSelective',
              ...this.dialog.data
            }
          })
        } else {
          let model = this.dialog.data
          await this.$api.modify({
            data: {
              mapperId: 'com.bosssoft.monitor.dao.JobExecutorThresholdMapper.updateByPrimaryKeySelective',
              fid: model.fid,
              statisticalCount: model.statisticalCount
            }
          })
        }
        this.dialog.loading = false
        this.featchData(this.global.groupId)
        this.dialog.show = false
        this.$message.success(this.dialogMode.message)
      } catch (error) {
        error.message && this.$message.error(error.message)
      }
    },
    /**
     启用 停用 操作
     */
    async toggleEnable (status) {
      if (this.grid.selectedRow.length === 0) {
        this.$message.info('请选择指标！')
        return
      }
      await this.$api.modify({
        data: {
          mapperId: 'com.bosssoft.monitor.dao.JobExecutorThresholdMapper.toggleEnable',
          status,
          ids: JSON.stringify(this.grid.selectedRow.map(item => item.fid))
        }
      })
      this.featchData()
    },
    /**
     任务执行器下拉框 更改监听
     */
    onJobNameChange (index) {
      this.dialog.data.jobName = this.jobExecutorSettins[index].jobName
      this.dialog.data.describe = this.jobExecutorSettins[index].describe
      this.dialog.data.executeFrequency = this.jobExecutorSettins[index].executeFrequency
    },
    /**
     获取下拉树 数据
     */
    async loadNode (node, resolve) {
      // 第一级节点 =》部署点
      if (node.level === 0) {
        let data = [{
          id: 'root',
          name: '执行器'
        }]
        return resolve(data)
      }
      // 叶节点 =》执行器
      if (node.level > 1) return resolve([])
      let { data } = await this.$api.queryData({
        params: {
          mapperId: 'com.bosssoft.monitor.dao.JobExecutorThresholdMapper.queryExecutors',
          id: node.data.id
        }
      })
      data.data.forEach((item) => {
        item.leaf = true
      })
      resolve(data.data)
    },
    // async loadNode (node, resolve) {
    //   // 第一级节点 =》部署点
    //   if (node.level === 0) {
    //     let { data } = await this.$api.queryData({
    //       params: {
    //         mapperId: 'com.bosssoft.monitor.dao.JobExecutorThresholdMapper.queryDeploymentPoints'
    //       }
    //     })
    //     return resolve(data.data)
    //   }
    //   // 叶节点 =》执行器
    //   if (node.level > 1) return resolve([])
    //   let { data } = await this.$api.queryData({
    //     params: {
    //       mapperId: 'com.bosssoft.monitor.dao.JobExecutorThresholdMapper.queryExecutors',
    //       id: node.data.id
    //     }
    //   })
    //   console.log(node)
    //   resolve(data.data)
    // },
    /**
     下拉树节点 点击事件
     */
    async handleNodeClick (data, node) {
      // 叶子节点点击
      if (node.level > 1) {
        // 获取data
        this.global.groupId = node.data.id
        this.global.executorName = node.data.name
        this.global.address = node.data.address
        this.featchData(this.global.groupId)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .taskrun-statistical-count {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
  }
  .el-tree {
    background: none;
  }
</style>
